@import "../colourScheme.css";

.morseSelectionPanel {
  width: 204px;
  height: 570px;
  color: var(--text);
  font-weight: 300;
  letter-spacing: 2px;
  margin: 20px 32px 20px 0px;
}

.morseSelectionPanel:last-of-type {
  margin: 20px 0px 20px 0px;
}

.morseSelectionPanelTitle {
  width: 100%;
  height: 20px;
  overflow: visible;
}

.morseButtonContainer {
  margin-top: 14px;
  margin-left: -6px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  width: 100%;
  height: calc(100% - 20px);
}

.morseSelectionPanel .panelActions {
  position: relative;
  display: inline-block;
  margin: 0px 0px 0px 8px;
  top: -2px;
  left: -4px;
  color: var(--textAlt);
  opacity: 0;
  overflow: visible;
  height: 1px;
  pointer-events: none;
  transition: opacity 0.2s ease, left 0.2s ease;
}

.morseSelectionPanel.custom:hover .panelActions {
  opacity: 0.8;
  left: 0px;
  pointer-events: all;
}

.morseSelectionPanel.custom:hover .panelActions:hover {
  opacity: 1;
}
